<template>
    <el-container class="app-container">
        <el-aside class="aside" width="auto">
            <v-aside></v-aside>
        </el-aside>
        <el-container class="is-vertical">
            <v-header></v-header>
            <el-main id="mainBox">
                <transition name="fade-transform" mode="out-in">
                    <keep-alive>
                        <router-view></router-view>
                    </keep-alive>
                </transition>
            </el-main>
        </el-container>
    </el-container>
</template>

<script type="text/ecmascript-6">
    import vHeader from './components/header.vue';
    import vAside from './components/aside.vue';
    import {mapMutations} from 'vuex'
    import {apiRoleList} from "@/api";
    import {logic} from "@/utils";
    import {$SYS_ERR} from "@/config/dictionary";

    export default {
        name: 'layout',
        data() {
            return {}
        },
        mounted() {
            apiRoleList()
                .then(res => {
                    logic(res, result => {
                        this['SET_ROLELIST'](result.data);
                    });
                })
                .catch(err => {
                    this.$message({
                        type: 'error',
                        message: $SYS_ERR
                    });
                    throw Error(err);
                })
        },
        methods: {
            ...mapMutations([
                'SET_ROLELIST'
            ]),
        },
        components: {
            vHeader,
            vAside
        }
    }
</script>

